	// 
	queryBatchCreditList() {
		// this.batchCreditBean.acDateBegin = this.commfunc.transDateN(this.batchCreditBean.acDateBegin);
		// this.batchCreditBean.acDateEnd = this.commfunc.transDateN(this.batchCreditBean.acDateEnd);
		// let param = this.batchCreditBean;
		let param = {
			groupId: this.batchCreditBean.groupId,
			groupName: this.batchCreditBean.groupName,
			groupType: this.batchCreditBean.groupType,
			custNo: this.batchCreditBean.custNo,
			custName: this.batchCreditBean.custName,
			state: this.batchCreditBean.state,
			bankId: this.batchCreditBean.bankId,//ID
			bankName: this.batchCreditBean.bankName,//
			// bankName: this.batchCreditBean.bankName,
			operName: this.batchCreditBean.operName,
			operId: this.batchCreditBean.operId,
			acDateBegin: this.commfunc.transDateN(this.batchCreditBean.acDateBegin),
			acDateEnd: this.commfunc.transDateN(this.batchCreditBean.acDateEnd),
			operType: "3",
			pageNum: this.batchCreditBean.pageNum,
			pageSize: this.batchCreditBean.pageSize,
			first: this.first,
		}
		// delete param.acDateBegin;
		// delete param
		// param['acDateBegin'] = this.commfunc.transDateN(this.batchCreditBean.acDateBegin);

		// this.batchCreditBean.operType = "3";
		// this.batchCreditBean.groupId = this.groupId;
		this.httpService.queryBatchPreCreditDetailList(param).subscribe(data => {
			if (data.returnCode.code == "success") {

				this.tableList = data.resultList;
				this.total = data.total;
				this.tableList.forEach(element => {
					element.loanOfficerAdvises = this.transMony(element.loanOfficerAdvises * 10000);
					element.loanSystemAdvises = this.transMony(element.loanSystemAdvises * 10000);
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: "" });
		})
	}
	// 
	transMony(param) {
		let result = "";
		if (param) {
			result = this.commfunc.transform(param) + '';
		}
		result = this.decimalPipe.transform(param, '1.2-2');//
		return result;
	}
	// 
	paginate(event) {

		this.batchCreditBean.pageSize = event.rows * 1;
		let currentPage = event.page + 1;
		this.batchCreditBean.pageNum = currentPage;
		this.first = event.page * this.batchCreditBean.pageSize;
		this.queryBatchCreditList();
	}


	//
	queryViewRelations(resolve, notApply) {
		let post_name = JSON.parse(this.commfunc.getSessionDataCH('postIdList'));
		const params = {
			businessType: "17",
			roleId: post_name,
			viewIndex: 1,
			viewSize: 100,
		};
		console.log(params)
		this.creditCcmService.queryViewRelations(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.list.length > 0) {
					let viewId = [];
					data.list.forEach(e => {
						// if (e.viewName.match('')) {
						viewId.push({ a: e.viewConfigId, b: e.viewName });
						// }
					});
					this.commfunc.setSessionDataCH('viewId', JSON.stringify(viewId));
					this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify({ businessType: "17", notApply: notApply })]);
				} else {
					this.msgs = [{ severity: 'info', summary: '', detail: ',' }];
				}
				// let param = ""

			} else {
				this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
		})
	}

