	// 
	queryBatch(item, self, notApply) {
		let batchDetail = new Promise(resolve => {
			this.queryBatchDetail(item, resolve)
		})
		let batchGroup = new Promise(resolve => {
			this.queryGroupInfo(item, resolve)
		})
		Promise.all([batchDetail, batchGroup]).then(res => {
			let datas = {
				detailData: res[0]["detailDtas"],
				groupData: res[1]["groupDtas"],
				self: self
			}
			console.log(datas)
			this.commfunc.setSessionDataCH('groupBranchData', JSON.stringify(datas))
			this.queryViewRelations(item, notApply);
		})
	}
	// 
	queryBatchDetail(item, resolve) {
		let params = {
			batchId: item.batchId
		}
		this.httpService.queryBatchPreCreditInfo(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				let datas = {
					batchId: data.batchId,
					operType: data.operType,
					state: data.state,
					groupId: data.groupId,
					groupName: data.groupName,
					productId: data.productId,
					productName: data.productName,
					guaranteeType: data.guaranteeType,
					contractType: data.contractType,
					endDate: data.endDate,
					rateKind: data.rateKind,
					returnKind: data.returnKind,
					note: data.note,
					operId: data.operId,
					operName: data.operName,
					bankId: data.bankId,
					bankName: data.bankName,
					acDate: data.acDate,
					Permissioncontrol:data.permissionControler
				}
				resolve({
					detailDtas: datas
				})

				// this.commfunc.setSessionDataCH('groupBranchData', JSON.stringify(datas))
				// this.queryViewRelations(item);
			} else {
				this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
		})
	}
	// queryCustGroupInfo
	queryGroupInfo(item, resolve) {
		let params = {
			"permissionFlag": "1",
			"advanceInput": "0",
			"groupType": "0",
			"pageNum": 1,
			"pageSize": 10,
			"conditionList": [
				[
					{
						"field": "group_id",
						"operator": "=",
						"value": item.groupId
					}
				]
			],

		}
		this.httpService.queryCustGroupInfo(params).subscribe(result => {
			if (result.returnCode.code == 'success') {
				let data = result.resultList[0] || {}
				console.log("oop")
				let datas = {
					cust_num: data.open_num || null,//
					valid_cust_num: data.valid_cust_num || null,//
					valid_cov_rate: data.valid_ratio || null,//
					crdt_cust_num: data.CRDT_CUST_NUM || null,//
					pre_crdt_amt: data.pre_credit_limit  || null,//
					// crdt_cust_num:data.cust_num || null,//
					pre_crdt_cust_num:data.PRE_CRDT_CUST_NUM,//  			
					crdt_amt: data.CRDT_AMT || null,//
					crdt_bal: data.CRDT_BAL || null,//
					loan_cust_num: data.LOAN_CUST_NUM || null,//
					// crdt_amt:data.cust_num || null,//
					CRDT_AMT_USED: data.CRDT_AMT_USED,// 0
					GROUP_ID: data.group_id || null,//
					GROUP_NAME: data.group_name || null,//
					GROUP_TYPE: data.group_type || null,//
					GROUP_OWNER_NAME: data.group_owner || null,//
					GROUP_LINKMANNAME: data.group_of_contacts || null,//
					GROUP_LINKMANID: data.group_of_contacts_id || null,//ID
					ORG_NAME: data.org_id || null,//
					ADDRESS: data.address_message || null,//
					PRODUCT_CODE: data.featured_products || null,//
					INT: data.INT || null,//
					GROUP_OWNER_ID: data.group_owner || null,//
					ORG_ID: data.create_org_id || null,//
					subGroupType: data.subGroupType || null //
				}
				resolve({
					groupDtas: datas
				})
			} else {
				this.msgs = [{ severity: 'error', summary: '', detail: result.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
		})
	}
	// 
	addBatch() {
		let params = {
			operType: "1",//
			groupId: this.groupId,//
			groupName: this.groupName,//
			groupType: this.groupType,//
		}
		this.httpService.addBatchPreCredit(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.batchCreditBean.pageNum = 1;
				this.batchCreditBean.pageSize = 10;
				this.first = 0;
				this.queryBatchCreditList();
				this.queryBatch({ groupId: this.groupId, batchId: data.batchId }, "1", "2");

			} else {
				this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
		})

		// this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify({ businessType: "17" })]);
	}
