import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';


@Component({
  selector: 'app-group-message-send',
  templateUrl: './group-message-send.component.html',
  styleUrls: ['./group-message-send.component.scss'],
  providers: [ConfirmationService],

})
export class GroupMessageSendComponent implements OnInit {

  // 
   msgs: Message[] = [];

  // 
   groupId: any = '';//
   groupName: any = '';//

  // 
   messageModelVal: any = '';

  // 
   SendMessageDisplay: boolean = false;
  

  constructor(
    public routerInfo: ActivatedRoute,
    public router: Router,
    public confirmationService: ConfirmationService,
  ) { }

  ngOnInit() {
    this.preSet();//
  }
  preSet() {
    // 
    // 
    if (this.routerInfo.snapshot.params['groupId'] && this.routerInfo.snapshot.params['groupId'] != '') {
      this.groupId = this.routerInfo.snapshot.params['groupId'];
    }
    if (this.routerInfo.snapshot.params['groupName'] && this.routerInfo.snapshot.params['groupName'] != '') {
      this.groupName = this.routerInfo.snapshot.params['groupName'];
    }
  }
  sendMessage() {
    // 
    this.SendMessageDisplay = true;
  }

  returnValue(event) {
    this.SendMessageDisplay = event.SendMessageDisplay;
    this.messageModelVal = event.returnData.messageTempleID;
  }

  onHid() {
    // 
    
  }
  // 
  returnCustomView() {
    // 
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
      }
    });
  }

}
